import React, { forwardRef } from 'react';
import { cn } from '@/utils/cn';

interface InputProps extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  error?: string;
  icon?: React.ReactNode;
  variant?: 'primary' | 'secondary' | 'danger';
  containerClassName?: string;
}

const Input = forwardRef<HTMLInputElement, InputProps>(
  (
    {
      label,
      error,
      icon,
      variant = 'primary',
      className,
      containerClassName,
      ...props
    },
    ref
  ) => {
    const variantClasses = {
      primary: 'focus:border-primary',
      secondary: 'focus:border-gray-400',
      danger: 'border-red-500 focus:border-red-500'
    };

    return (
      <div className={containerClassName}>
        {label && (
          <label className="mb-2.5 block font-medium text-black dark:text-white">
            {label}
          </label>
        )}
        <div className="relative">
          {icon && (
            <div className="absolute left-4 top-1/2 -translate-y-1/2">
              {icon}
            </div>
          )}
          <input
            ref={ref}
            className={cn(
              'w-full rounded-lg border border-stroke bg-transparent px-4 py-3 outline-none transition',
              'focus-visible:shadow-none dark:border-gray-600 dark:bg-gray-900',
              icon ? 'pl-12' : '',
              variantClasses[variant],
              error && '!border-red-500',
              className
            )}
            {...props}
          />
        </div>
        {error && (
          <span className="mt-1 block text-sm text-red-500">{error}</span>
        )}
      </div>
    );
  }
);

Input.displayName = 'Input';

export default Input;
