'use client';

import React, { useEffect, useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { services } from '@/services/api';
import { CompraPlano } from '@/services/api/plano.service';
import { formatCurrency, formatDate } from '@/utils/format';
import { 
  CreditCardIcon,
  CheckCircleIcon,
  ClockIcon,
  ExclamationTriangleIcon,
  QrCodeIcon
} from '@heroicons/react/24/outline';
import { CopyIcon, QrcodeIcon } from '@/icons';
import Button from '@/components/ui/button/Button';
import { Modal } from '@/components/ui/modal';
import toast from 'react-hot-toast';
import { Card } from '@/components/ui/card';
import Badge from '@/components/ui/badge/Badge';
import Label from '@/components/form/Label';
import Input from '@/components/ui/Input';
import Image from 'next/image';

interface PlanoAtualData {
  planoAtual: CompraPlano | null;
  comprasPendentes: CompraPlano[];
}

export default function PlanoAtualPage() {
  const router = useRouter();
  const [data, setData] = useState<PlanoAtualData>({ planoAtual: null, comprasPendentes: [] });
  const [loading, setLoading] = useState(true);
  const [isQRModalOpen, setIsQRModalOpen] = useState(false);
  const [selectedCompra, setSelectedCompra] = useState<CompraPlano | null>(null);
  const [gerandoCobranca, setGerandoCobranca] = useState(false);
  const [mounted, setMounted] = useState(false);

  const loadPlanoAtual = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.compraPlano.getPlanoAtual();
      setData(response.data);
    } catch (error) {
      console.error('Erro ao carregar plano atual:', error);
      toast.error('Erro ao carregar informações do plano');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    setMounted(true);
    loadPlanoAtual();
  }, [loadPlanoAtual]);

  const handleGerarCobranca = async (compra: CompraPlano) => {
    try {
      setGerandoCobranca(true);
      const response = await services.compraPlano.gerarCobranca(compra.id);
      toast.success('Cobrança gerada com sucesso!');
      loadPlanoAtual(); // Recarregar dados
    } catch (error: any) {
      console.error('Erro ao gerar cobrança:', error);
      toast.error(error.response?.data?.error || 'Erro ao gerar cobrança');
    } finally {
      setGerandoCobranca(false);
    }
  };

  const handleShowQRCode = (compra: CompraPlano) => {
    setSelectedCompra(compra);
    setIsQRModalOpen(true);
  };

  const handleCopyPix = async (copiacola: string) => {
    try {
      await navigator.clipboard.writeText(copiacola);
      toast.success('Código PIX copiado para a área de transferência!');
    } catch (error) {
      toast.error('Erro ao copiar código PIX');
    }
  };

  const handleCopyBoleto = async (codigobarra: string) => {
    try {
      await navigator.clipboard.writeText(codigobarra);
      toast.success('Código de barras copiado para a área de transferência!');
    } catch (error) {
      toast.error('Erro ao copiar código de barras');
    }
  };

  if (loading) {
    return (
      <div className="p-4 sm:p-6 lg:p-8">
        <div className="animate-pulse space-y-6">
          <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded w-1/3"></div>
          <div className="h-48 bg-gray-200 dark:bg-gray-700 rounded"></div>
          <div className="h-32 bg-gray-200 dark:bg-gray-700 rounded"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
          Plano Atual
        </h1>
        <p className="text-sm text-gray-500">
          Visualize seu plano atual e compras pendentes
        </p>
      </div>

      {/* Plano Atual */}
      {data.planoAtual ? (
        <Card className="p-6 mb-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
              Plano Ativo
            </h2>
            <Badge color="success">
              Ativo
            </Badge>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="flex items-center gap-3">
              <CreditCardIcon className="h-6 w-6 text-green-500" />
              <div>
                <p className="text-sm text-gray-500 dark:text-gray-400">Plano</p>
                <p className="font-semibold text-gray-900 dark:text-white">
                  {data.planoAtual.plano?.nome || 'N/A'}
                </p>
              </div>
            </div>
            
            <div className="flex items-center gap-3">
              <CheckCircleIcon className="h-6 w-6 text-green-500" />
              <div>
                <p className="text-sm text-gray-500 dark:text-gray-400">Valor Pago</p>
                <p className="font-semibold text-gray-900 dark:text-white">
                  {formatCurrency(data.planoAtual.valor)}
                </p>
              </div>
            </div>
            
            <div className="flex items-center gap-3">
              <ClockIcon className="h-6 w-6 text-green-500" />
              <div>
                <p className="text-sm text-gray-500 dark:text-gray-400">Data do Pagamento</p>
                <p className="font-semibold text-gray-900 dark:text-white">
                  {data.planoAtual.dataPagamento ? formatDate(data.planoAtual.dataPagamento) : 'N/A'}
                </p>
              </div>
            </div>
          </div>
        </Card>
      ) : (
        <Card className="p-6 mb-6">
          <div className="text-center py-8">
            <ExclamationTriangleIcon className="h-12 w-12 text-yellow-500 mx-auto mb-4" />
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
              Nenhum Plano Ativo
            </h3>
            <p className="text-gray-500 dark:text-gray-400 mb-4">
              Você ainda não possui um plano ativo. Contrate um plano para começar a usar o sistema.
            </p>
            <Button
              variant="primary"
              onClick={() => router.push('/dashboard/empresa/planos/contratar')}
            >
              Contratar Plano
            </Button>
          </div>
        </Card>
      )}

      {/* Compras Pendentes */}
      {data.comprasPendentes.length > 0 && (
        <div className="mb-6">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
            Compras Pendentes
          </h2>
          
          <div className="space-y-4">
            {data.comprasPendentes.map((compra) => (
              <Card key={compra.id} className="p-6">
                <div className="flex items-center justify-between mb-4">
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900 dark:text-white">
                      {compra.plano?.nome || 'Plano'}
                    </h3>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      Contratado em {formatDate(compra.createdAt)}
                    </p>
                  </div>
                  <Badge color="warning">
                    Pendente
                  </Badge>
                </div>
                
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
                  <div>
                    <p className="text-sm text-gray-500 dark:text-gray-400">Valor</p>
                    <p className="font-semibold text-gray-900 dark:text-white">
                      {formatCurrency(Number(compra.valor))}
                    </p>
                  </div>
                  
                  <div>
                    <p className="text-sm text-gray-500 dark:text-gray-400">Vencimento</p>
                    <p className="font-semibold text-gray-900 dark:text-white">
                      {compra.vencimento ? formatDate(compra.vencimento) : 'N/A'}
                    </p>
                  </div>
                  
                  <div>
                    <p className="text-sm text-gray-500 dark:text-gray-400">Status</p>
                    <p className="font-semibold text-gray-900 dark:text-white">
                      {compra.status === 'PENDENTE' ? 'Aguardando Pagamento' : 'Pago'}
                    </p>
                  </div>
                </div>
                
                <div className="flex gap-3">
                  {!compra.copiacola && !compra.codigobarra ? (
                    <Button
                      variant="primary"
                      onClick={() => handleGerarCobranca(compra)}
                      disabled={gerandoCobranca}
                    >
                      {gerandoCobranca ? 'Gerando...' : 'Gerar Cobrança'}
                    </Button>
                  ) : (
                    <>
                      {compra.copiacola && (
                        <>
                          <Button
                            variant="outline"
                            onClick={() => handleCopyPix(compra.copiacola!)}
                            startIcon={<CopyIcon />}
                          >
                            Copiar PIX
                          </Button>
                          <Button
                            variant="outline"
                            onClick={() => handleShowQRCode(compra)}
                            startIcon={<QrcodeIcon />}
                          >
                            Ver QR Code
                          </Button>
                        </>
                      )}
                      
                      {compra.codigobarra && (
                        <Button
                          variant="outline"
                          onClick={() => handleCopyBoleto(compra.codigobarra!)}
                          startIcon={<CopyIcon />}
                        >
                          Copiar Boleto
                        </Button>
                      )}
                    </>
                  )}
                </div>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Botão para contratar mais planos */}
      <div className="text-center">
        <Button
          variant="outline"
          onClick={() => router.push('/dashboard/empresa/planos/contratar')}
        >
          Contratar Outro Plano
        </Button>
      </div>

      {/* Modal QR Code */}
      {mounted && (
        <Modal
          isOpen={isQRModalOpen}
          onClose={() => {
            setIsQRModalOpen(false);
            setSelectedCompra(null);
          }}
          className="max-w-[500px] m-4"
        >
          <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
            <div className="px-2 pr-14">
              <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
                QR Code PIX
              </h4>
              <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
                Escaneie o QR Code para realizar o pagamento.
              </p>
            </div>
            
            {selectedCompra && (
              <div className="space-y-4">
                <div className="text-center">
                  <h5 className="font-semibold text-gray-900 dark:text-white mb-2">
                    {selectedCompra.plano?.nome}
                  </h5>
                  <p className="text-lg font-bold text-gray-900 dark:text-white mb-4">
                    {formatCurrency(selectedCompra.valor)}
                  </p>
                </div>
                
                {selectedCompra.qrcode ? (
                  <div className="text-center">
                    <Image 
                      src={selectedCompra.qrcode?.startsWith('data:') ? selectedCompra.qrcode : `data:image/png;base64,${selectedCompra.qrcode}`}
                      alt="QR Code PIX" 
                      width={100}
                      height={100}
                      className="mx-auto max-w-full h-auto"
                      loading="lazy"
                    />
                  </div>
                ) : (
                  <div className="text-center py-8">
                    <QrCodeIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                    <p className="text-gray-500 dark:text-gray-400">
                      QR Code não disponível
                    </p>
                  </div>
                )}
                
                {selectedCompra.copiacola && (
                  <div className="space-y-2">
                    <Label>Código PIX</Label>
                    <div className="flex gap-2">
                      <Input
                        value={selectedCompra.copiacola}
                        readOnly
                        className="flex-1"
                      />
                      <Button
                        variant="outline"
                        onClick={() => handleCopyPix(selectedCompra.copiacola!)}
                        startIcon={<CopyIcon />}
                      >
                        Copiar
                      </Button>
                    </div>
                  </div>
                )}
              </div>
            )}

            <div className="flex justify-end gap-3 pt-4">
              <Button
                type="button"
                variant="outline"
                onClick={() => {
                  setIsQRModalOpen(false);
                  setSelectedCompra(null);
                }}
              >
                Fechar
              </Button>
            </div>
          </div>
        </Modal>
      )}
    </div>
  );
}
